<template>
    <b-container fluid style=" background-color: white;">
      <b-overlay :show="!productLoaded" spinner-large
        spinner-variant="primary" spinner-type="grow" rounded="sm" style="min-height:500px">
        <!-- <b-container fluid class="mx-auto text-center" v-if="productLoaded" style=" ">
          <h1><p class="mt-2 mb-n1">{{ product.name }}</p></h1>
            <b-form-rating inline no-border
                variant="warning"
                style=""
                readonly
                size="lg"
                v-model="product.rating"
                precision="1">
            </b-form-rating>
        </b-container> -->
        <b-container>
            <b-row>
                <b-col cols="12" md="4" class="my-auto">
                    <b-carousel
                        id="carousel-1"
                        :interval="4000"
                        controls
                        indicators
                        img-width="200"
                        img-height="200"
                        style="text-shadow: 1px 1px 2px #333;vertical-align:center;"
                        @sliding-start="onSlideStart"
                        @sliding-end="onSlideEnd"
                    >
                      <b-carousel-slide
                        v-for="(image,index) in product.images" :key="index">
                        <template v-slot:img>
                          <img width="200"
                            height="300"
                            :src="image.link"
                            alt="Product Images"
                            class="d-block w-100"
                          >
                        </template>
                      </b-carousel-slide>
                    </b-carousel>
                </b-col>
                <b-col cols="12" md="5" class="d-flex flex-column mt-2 mt-md-0" v-if="productLoaded">
                  <b-container fluid class="" style=" ">
                    <h3><p class="mt-2 mb-n1">{{ product.name }}</p></h3>
                    <b-form-rating inline no-border class="mr-auto"
                        variant="warning"
                        style="transform:scale(0.8,0.8)"
                        readonly
                        v-model="product.rating"
                        precision="2">
                    </b-form-rating>
                    <hr>
                    <div class="" id="cur-price">&nbsp;&nbsp;&#2547;{{product.price.sellingPrice}}</div>
                    <div id="prev-price" class="">
                      <span class="strike">&#2547;{{ Math.round(product.price.sellingPrice / (1 -  product.price.discount/ 100)) }}</span>
                      <strong id="discount">&nbsp;-{{ product.price.discount }}%</strong>
                    </div>
                    <hr>
                    <b-row v-for="([s,values],i) in Object.entries(product.specifications)" :key="i">
                      <template v-if="Array.isArray(values) && values.length > 1">
                        <b-col cols="3" class="mb-5">{{s}}</b-col>
                        <b-col >
                          <b-button disabled variant="outline-secondary" v-for="(spec,index) in values" :key="index" class="mr-2">{{spec}}</b-button>
                        </b-col>
                      </template>
                    </b-row>
                    <b-row>
                      <b-col cols="3" class="mb-5">Color</b-col>
                      <b-col >
                          <b-button disabled variant="outline-secondary" v-for="(color,i) in colors"
                             :key="i" class="mr-2">
                            {{color}}
                          </b-button>
                        </b-col>
                    </b-row>
                  </b-container>
                </b-col>
                <b-col v-if="productLoaded">
                  <b-row class="text-secondary"><div style="font-size:20px;">Delivery Options</div></b-row>
                  <b-row>
                    <b-col cols="1"><b-icon icon="geo-alt"></b-icon></b-col>
                    <b-col class="my-auto"><div style="font-size:15px;">{{address}}</div></b-col>
                  </b-row>
                  <hr>
                  <b-row>
                    <b-col cols="1"><b-icon icon="house-door"></b-icon></b-col>
                    <b-col cols="8" class="my-auto"><div style="font-size:15px;">Home Delivery</div></b-col>
                    <b-col class="my-auto">&nbsp;&nbsp;&#2547;{{deliveryFee}}</b-col>
                  </b-row>
                  <b-row class="mt-4">
                    <b-col cols="1">
                      <b-icon icon="cash" variant="success"></b-icon>
                      </b-col>
                    <b-col class="my-auto"><div style="font-size:15px;">Cash On Delivery Available</div></b-col>
                  </b-row>
                  <hr>
                  <b-row class="text-secondary"><div style="font-size:20px;">Return and warranty</div></b-row>
                  <b-row>
                    <b-col cols="1"><b-icon icon="geo-alt"></b-icon></b-col>
                    <b-col class="my-auto">
                      <div style="font-size:15px;">{{product.price.warranty}} days return</div>
                    </b-col>
                  </b-row>
                  <b-row>
                    <b-col cols="1">
                      <b-iconstack v-if="product.price.refundable">
                        <b-icon icon="shield"  variant="success"></b-icon>
                        <b-icon icon="check"  variant="success"></b-icon>
                      </b-iconstack>
                      <b-icon icon="shield-slash" variant="danger" v-else></b-icon>
                    </b-col>
                    <b-col>
                      <div style="font-size:15px;padding-top:4px;" v-if="product.price.refundable">100% refund</div>
                      <div style="font-size:15px;" v-else>Warranty not available</div>
                    </b-col>
                  </b-row>
                  <hr>
                </b-col>
            </b-row>
            <div class="mx-auto text-center col-md-4" v-if="productLoaded">
              <div>
                <b-button
                  id="add-to-cart"
                  :aria-controls="'sidebar-right-footer'+product.productID"
                  :aria-expanded="show"
                  @click="initialHandler(true)"
                  squared
                  style="border: 0;"
                >
                  Add to Bag
                </b-button>
                <b-button
                  id="buy-now"
                  :aria-controls="'sidebar-right-footer-buy'+product.productID"
                  aria-expanded=""
                  @click="initialHandler(false)"
                  squared
                  style="border: 0;"
                >
                  Buy Now
                </b-button>
                <b-sidebar
                  :id="'sidebar-right-footer'+product.productID"
                  aria-label="Sidebar with custom footer"
                  v-model="show"
                  shadow
                  lazy
                >
                  <template v-slot:title>
                    <span class="">There are some options.<br>Choose yours:</span>
                  </template>
                  <template v-slot:footer>
                    <div class="mx-auto text-center">
                      <b-button squared variant="warning" @click="doReset" class="col-6">Reset</b-button>
                      <b-button @click="finalHandler" squared variant="success" class="col-6" :disabled="!allSelected">
                        {{cartButtonPressed ? 'Add to Bag' : 'Check Out'}}
                      </b-button>
                    </div>
                  </template>
                  <b-overlay :show="!productLoaded" opacity="0.4" rounded="sm" style="height:500px">
                    <template v-slot:overlay>
                      <div class="text-center">
                        <b-icon icon="stopwatch" font-scale="3" animation="cylon"></b-icon>
                        <p id="cancel-label">Please wait...</p>
                      </div>
                    </template>
                    <div class="mx-auto d-flex flex-column" v-if="productLoaded">
                      <b-list-group>
                        <span
                          v-for="(values, key, index) in product.specifications"
                          :key="index">
                          <b-list-group-item v-if="Array.isArray(values) && values.length > 1">
                            <b-form-group :label="key">
                            <b-form-select style="width:80%"
                                          v-model="selector[key]" :options="values" :value-field="key">
                              <template v-slot:first>
                                <b-form-select-option :value="null" disabled>-- Please select an option --</b-form-select-option>
                              </template>
                            </b-form-select></b-form-group>
                          </b-list-group-item>
                        </span>
                        <b-list-group-item>
                          <label :for="'sb-inline' + product.productID">Quantity</label>
                          <b-form-spinbutton class="ml-2" :id="'sb-inline' + product.productID" v-model="quantity" inline></b-form-spinbutton>
                        </b-list-group-item>
                      </b-list-group>
                    </div>
                  </b-overlay>
                </b-sidebar>
              </div>
            </div>
            <hr>
            <div v-if="productLoaded">
              <h4>Specifications</h4>
              <ul>
                <span v-for="([s,values],i) in Object.entries(product.specifications)" :key="i">
                  <template v-if="!(Array.isArray(values) && values.length > 1) && s!=='Description'">
                    <li>{{s}}:{{Array.isArray(values) ? values[0] : values}}</li>
                  </template>
                </span>
                <li>BrandName: {{product.brandName}}</li>
              </ul>
              <hr>
              <div>
                <h4>Overview</h4>
                {{product.specifications.Description}}
                </div>
            </div>
            <hr>
            <div v-if="productLoaded && product.rating">
              <h4>Ratings and Reviews</h4>
              <b-row>
                <b-col cols="12" md="4" class="my-auto">
                  <b-container fluid class="mx-auto text-center">
                    <h1 class="mx-auto mb-n1 text-center">{{ product.rating.toFixed(2)}}<small style="font-size:15px;">/5</small></h1>
                    <b-form-rating inline no-border
                      variant="warning"
                      style=""
                      readonly
                      size="lg"
                      v-model="product.rating"
                      precision="2" class="mx-auto text-center">
                    </b-form-rating>
                  </b-container>
                </b-col>
                <b-col>
                  <div v-for="i in 5" :key="i" class="d-flex d-inline">
                    <b-form-rating inline no-border
                      variant="warning"
                      style=""
                      readonly
                      size="sm"
                      :value="6-i"
                      precision="1" class="my-auto">
                    </b-form-rating>
                    <b-progress :max="reviews.length" :value="stat[6-i-1]" height=""
                      class="w-25 my-auto" variant="warning"></b-progress>
                      <small class="ml-1 my-auto">{{stat[6-i-1]}}</small>
                  </div>
                </b-col>
              </b-row>
              <hr class="d-lg-none">
              <b-container fluid v-for="(review,i) in reviews" :key="i" class="mb-2 shadow-sm" style="">
                <b-row class="mr-auto">
                  <b-form-rating inline no-border
                      variant="warning"
                      style=""
                      readonly
                      size="sm"
                      :value="review.rating"
                      precision="1">
                  </b-form-rating>
                </b-row>
                <b-row class="mr-auto ml-1"><small>by {{ review.name }}</small></b-row>
                <b-row class="mr-auto ml-1"><small class="text-italic">{{ convertToDate(review.date._seconds) }}</small></b-row>
                <b-row class="mt-2 mr-auto ml-1"><p>{{review.review}}</p></b-row>
              </b-container>
            </div>
            <div class="mx-auto bd-highlights text-center" v-if="productLoaded && relatedProducts.length">
                <h3>Related Products</h3>
            </div>
        </b-container></b-overlay><b-container>
        <ProductScrollSection :products="relatedProducts"></ProductScrollSection></b-container>
        <b-container class="mx-auto bd-highlights text-center" v-if="productLoaded && relatedBrands.length">
            <h3>Related Brands</h3>
            <b-row cols-lg="5" class="my-4">
              <b-col v-for="(brand,idx) in relatedBrands" cols="6" sm="4" md="3" :key="idx" class="mb-3">
                <brand-box :brand="brand"></brand-box>
              </b-col>
            </b-row>
        </b-container>
    </b-container>
</template>
<script>
import ProductScrollSection from '../../components/ProductScrollSection.vue'
import BrandBox from '../../components/Boxes/BrandBox'
import { mapGetters, mapActions } from 'vuex'
import api from '../../plugins/axios'
import loader from '../../mixins/loader'

export default {
  name: 'CertainProduct',
  data () {
    return {
      // i: 0,
      // value: 3.5,
      // prevPrice: 500,
      // curPrice: 400,
      // discount: -20,
      // selected: null,
      // ProductName: 'Facebook',
      // coloroptions: ['red', 'green', 'yellow'],
      // sizeoptions: ['L', 'XL', 'XXL'],
      // images: [
      //   'https://static-01.daraz.com.bd/p/7883ac9ce3be09d4e7e9e44f139e8d68.jpg',
      //   'https://static-01.daraz.com.bd/p/7883ac9ce3be09d4e7e9e44f139e8d68.jpg'
      // ],
      // totalimage: 2,
      // value: 4,
      // from here
      sliding: null,
      product: {},
      productLoaded: false,
      relatedProducts: [],
      selector: {},
      show: false,
      cartButtonPressed: null,
      quantity: 1,
      relatedBrands: [],
      reviews: [],
      stat: [],
      colors: []
    }
  },
  computed: {
    ...mapGetters([
      'authToken',
      'user'
    ]),
    allSelected () {
      if (!this.productLoaded) {
        return false
      }
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.product.specifications[key]) && this.product.specifications[key].length > 1 && this.selector[key] === null) {
            return false
          }
        }
      }
      return true
    },
    deliveryFee () {
      return this.user.district ? (this.user.district === 'Bogura' ? 0 : 60) : 0
    },
    address () {
      return this.user.address ? this.user.address + ' , ' + this.user.district : 'Bogura'
    }
  },
  components: {
    ProductScrollSection,
    BrandBox
  },
  mixins: [
    loader
  ],
  methods: {
    ...mapActions([
      'addToCart'
    ]),
    onSlideStart (slide) {
      this.sliding = true
    },
    onSlideEnd (slide) {
      this.sliding = false
    },
    showPrev () {
      if (this.i === 0) this.i = this.totalimage
      this.i = (this.i - 1) % this.totalimage
    },
    showNext () {
      this.i = (this.i + 1) % this.totalimage
    },
    loadProduct (id) {
      this.loader('/product', { productID: id }, `product-${id}`)
        .then(response => {
          this.product = response.data
          this.product.specifications = this.product.extra
          delete this.product.extra
          this.productLoaded = true
          this.colors = this.product.colors
          this.loadReviews()
          this.loadRelatedProducts()
          this.loadRelatedBrands()
          this.loadSelector()
        })
    },
    loadReviews () {
      const productID = this.product.productID
      this.loader(
        '/product/reviews',
        { productID },
        `reviews of product-${productID}`
      )
        .then(response => {
          this.reviews = response.data
          this.calculateStats()
        })
    },
    convertToDate (timestamp) {
      const unixTimestamp = timestamp
      const milliseconds = unixTimestamp * 1000
      const dateObject = new Date(milliseconds)
      const dateFormat = dateObject.toLocaleString()
      return dateFormat
    },
    calculateStats () {
      // for (let i = 0; i < this.reviews.length; i++) {
      //   this.stat[this.reviews[i].rating - 1]++
      // }
      this.stat = this.product.ratingDetails
    },
    loadRelatedProducts () {
      api.post('/search', { productIds: this.product.relatedProductIDs })
        .then(response => {
          console.log(`products related to ${this.product.productID} loaded`)
          console.log(response)
          this.relatedProducts = response.data.products
        })
        .catch(error => {
          console.log(`products related to ${this.product.productID} failed loading`)
          console.log(error.response)
        })
    },
    loadRelatedBrands () {
      const categoryName = this.product.category
      const offset = this.relatedBrands.length
      const limit = 5
      this.loader(
        '/category/brands',
        { 'cat-name': categoryName, offset, limit },
        `brands of ${categoryName} for product-${this.product.productID}`
      )
        .then(response => {
          this.relatedBrands = response.data
        })
    },
    loadSelector () {
      this.selector = JSON.parse(JSON.stringify(this.product.specifications)) // deep(disconnected) copy
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.product.specifications[key]) && this.product.specifications[key].length > 1) {
            this.selector[key] = null
          }
        }
      }
    },
    makeOrder () {
      const curPrice = this.product.price.sellingPrice
      return {
        products: [{
          id: this.product.productID,
          name: this.product.name,
          specifications: this.selector,
          quantity: this.quantity,
          image: this.product.images[0],
          curPrice,
          discount: this.product.price.discount,
          vat: this.product.price.vat
        }],
        subTotal: curPrice * (1 + this.product.price.vat / 100) * this.quantity
      }
    },
    initialHandler (cartButtonPressed) {
      if (!this.authToken && !cartButtonPressed) {
        this.$bvModal.show('login-modal')
      } else {
        this.loadProduct()
        this.show = true
        this.cartButtonPressed = cartButtonPressed
      }
    },
    finalHandler () {
      if (!this.allSelected) {
        return
      }
      if (this.cartButtonPressed) {
        this.addToCart([{
          productID: this.product.productID,
          specifications: this.selector,
          quantity: this.quantity,
          curPrice: this.product.price.sellingPrice,
          discount: this.product.price.discount,
          image: this.product.images[0],
          name: this.product.name,
          vat: this.product.price.vat
        }]).then().catch(() => {})
      } else {
        this.$router.push({
          name: 'OrderSubmitDetails',
          query: {
            order: this.makeOrder()
          }
        })
      }
      this.show = false
    },
    doReset () {
      for (const key in this.selector) {
        // eslint-disable-next-line no-prototype-builtins
        if (this.selector.hasOwnProperty(key)) {
          if (Array.isArray(this.product.specifications[key]) && this.product.specifications[key].length > 1) {
            this.selector[key] = null
          }
        }
      }
    }
  },
  watch: {
    '$route' (to, from) {
      this.loadProduct(to.params.id)
    }
  },
  created () {
    this.loadProduct(this.$route.params.id)
  }
}
</script>
<style>
    #prev-price {
        color: grey;
    }
    #discount {
        color: red;
    }
    #cur-price {
        font-size: 200%;
        color: darkorange;
    }
    #add-to-cart {
      width: 50%;
      background-color: #ff9a00;
      color: white;
      white-space: nowrap;
      text-align: center;
      padding-left: 0;
      padding-right: 0;
    }
    #buy-now {
      width: 50%;
      background-color: #ff4747;
      color: white;
      white-space: nowrap;
      text-align: center;
      padding-left: 0;
      padding-right: 0;
    }
</style>
